//-----------------------------------------------------------------------------------//
// Link mixin
//-----------------------------------------------------------------------------------//
@mixin link {
	color: $link-color;
	text-decoration: $link-hover-decoration;
	padding: 0;
	margin: 0;
	outline: none;

	&:hover,
	&:focus {
		color: $link-hover-color;
		text-decoration: $link-hover-decoration;
	}
	&:visited {
		color: $link-color;
	}
}

//-----------------------------------------------------------------------------------//
// Button mixin
//-----------------------------------------------------------------------------------//

@mixin btn {
	width: auto;
	border: 0 none;
	font-size: $button-font-size;
	font-family: $font-family-base;
	font-weight: $font-weight-light;
	border-radius: 3px;
	padding: .4rem 1rem;
	line-height: 1.5;
	display: inline-block;
	border-radius: 3px;

	&:hover {
		border: 0 none;
	}
}
// Primary button
@mixin btn-primary-mixin {
	@include btn();
	color: $white !important;
	background-color: $brand-primary;

	&:hover,
	&:focus {
		background-color: lighten( $brand-primary, 10% );
	}
}

// Secondary button
@mixin btn-secondary-mixin {
	@include btn();
	color: $white !important;
	background-color: $gray-dark;

	&:hover,
	&:focus {
		background-color: lighten( $gray-dark, 10% );
	}
}

// Primary outline button
@mixin btn-outline-primary-mixin {
	@include btn();
	color: $brand-primary !important;
	border: 1px solid $brand-primary !important;
	background: transparent;
	background-color: transparent;
	font-weight: $font-weight-light;

	&:hover,
	&:focus {
		color: #fff !important;
		background: $brand-primary;
		background-color: $brand-primary;
		border: 1px solid $brand-primary !important;
	}
}

// Secondary outline button
@mixin btn-outline-secondary-mixin {
	@include btn();
	color: $gray-dark !important;
	border: 1px solid $gray-dark !important;
	background: transparent;
	background-color: transparent;
	font-weight: $font-weight-light;

	&:hover,
	&:focus {
		color: #fff !important;
		background: $gray-dark;
		background-color: $gray-dark;
		border: 1px solid $gray-dark !important ;
	}
}

// Button small
@mixin btn-sm-mixin {
	font-size: 0.868rem !important;
	padding: .3rem 0.5rem !important;
}

// WordPress primary button
@mixin wordpress-button-primary-mixin {
	@include btn-primary-mixin();
	height: auto;
	margin: 0;
	float: none !important;

	@media screen and (min-width: 1200px) {
	  float: left;
	  margin: 0 10px 0 0;
	}
}

//-----------------------------------------------------------------------------------//
// Form mixins
//-----------------------------------------------------------------------------------//
@mixin label-mixin {
  font-family: $font-family-base;
  font-weight: $font-weight-light;
  font-size: 1.125rem;
  margin-top: 0;
  margin-bottom: 0;
}

@mixin label-top {
  width: 100%;
  display: block;
}

@mixin field-defaults {
    font-family: $font-family-base;
    font-weight: $font-weight-light;
    font-size: $font-size-base;
    line-height: 1.25;
    color: $body-color;
    margin-bottom: 15px;
    padding: 5px 12px;
    border: 1px solid $gray-medium;
    border-radius: 1px !important;
	 appearance:none;
    -moz-appearance:none; /* Firefox */
    -webkit-appearance:none; /* Safari and Chrome */
    box-shadow: none;
    box-sizing: border-box;
}

@mixin input-defaults {
	height: 34px;
	width: auto;
	display: inline-block;
}

@mixin checkbox-defaults {
	 appearance:checkbox;
    -moz-appearance:checkbox; /* Firefox */
    -webkit-appearance:checkbox; /* Safari and Chrome */
	 margin: 2px 10px 3px 0;
	 display: inline-block;
}

@mixin radio-defaults {
	 appearance:radio;
    -moz-appearance:radio; /* Firefox */
    -webkit-appearance:radio; /* Safari and Chrome */
 	 margin: 1px 10px 3px 0;
}

@mixin textarea-defaults {
	width: 100%;
	height: auto;
	min-height: 150px;
}

@mixin select-defaults {
    width: 100%;
    background-color: #fff;
    background: url(../images/icons/chevron-down-grey.png) no-repeat 97% 50% #fff;
    padding-right: 43px;
}

//-----------------------------------------------------------------------------------//
// Typography mixins
//-----------------------------------------------------------------------------------//
@mixin txt-small {
  font-size: 85%;
}

//-----------------------------------------------------------------------------------//
// Table mixins
//-----------------------------------------------------------------------------------//
@mixin responsive-table {
  @media screen and (max-width: 1200px) {
	  width: 100%;
	  display: block;
	}
}
//-----------------------------------------------------------------------------------//
// Panel notices
//-----------------------------------------------------------------------------------//
@mixin panel-notices-mixin {
	padding: 25px;
	border: 1px solid $gray-light;
	border-radius: 0;
	background: #fff;
}
//-----------------------------------------------------------------------------------//
// Tabs
//-----------------------------------------------------------------------------------//
@mixin horizontal-tab-parent {
	margin: 15px 0 0 0;
	padding: 0;
	border-bottom: 1px solid #EAEDF0;
	list-style-type: none;
}
@mixin horizontal-wrapper {
	margin-bottom: -1px;
}
@mixin horizontal-tab {
	color: $gray-dark;
	padding: 4px 20px;
	border-radius: 2px 2px 0px 0px;
	background: $gray-lightest;
	border-top: 1px solid $gray-light;
	border-left: 0 none;
	border-right: 1px solid $gray-light;
	border-bottom: 1px solid $gray-light;
	font-size: $font-size-base;
	font-weight: $font-weight-normal;

}
@mixin horizontal-tab-hover {
	border-color: $gray-light;
	background: #ffffff;
}
@mixin horizontal-tab-selected {
	background: #ffffff;
	border-bottom: 1px solid #ffffff;
}
@mixin horizontal-tab-first {
	border-left: 1px solid $gray-light;
}
