/*!
 * Core variables
 */
@import "variables.scss";
@import "mixins.scss";

/*-----------------------------------------------------------------------------------
 * Dashboard Design
 *
 * This is CSS sheet is for the new styling of the dashboard for later integration
 * in to the main CSS. When integrated some of the '!important' declarations
 * can be removed.
 *
/*-----------------------------------------------------------------------------------*/

//-----------------------------------------------------------------------------------//
// Framework
//-----------------------------------------------------------------------------------//

// Bootstap 3 overrides
#updraftcentral_dashboard {
	
	.container,
	.row {
			&:before,
			&:after {
				content: none !important;
			}
	}

	.col {
		-ms-flex-preferred-size: 0;
		flex-basis: 0;
		-ms-flex-positive: 1;
		flex-grow: 1;
		max-width: 100%;
	}
}
// Flex column order
.order-1 {
	-ms-flex-order: 1;
	order: 1;
}

.order-2 {
	-ms-flex-order: 2;
	order: 2;
}

.order-3 {
	-ms-flex-order: 3;
	order: 3;
}

.order-4 {
	-ms-flex-order: 4;
	order: 4;
}

.order-last {
	-ms-flex-order: 100;
	order: 100;
}

//-----------------------------------------------------------------------------------//
// Set up
//-----------------------------------------------------------------------------------//
html {
	font-size: 16px !important;
	margin: 0 !important;
}

// Remove browser outline glow
button,
textarea,
input {
  outline: none !important;
}

#wpadminbar {
	visibility: hidden !important;
	display: none !important;
}

//-----------------------------------------------------------------------------------//
// Structure
//-----------------------------------------------------------------------------------//
body {
  background: $body-bg !important;
  font-family: $font-family-base !important;
	font-weight: $font-weight-normal !important;
	font-size: $font-size-base !important;
	color: $body-color !important;
}

//-----------------------------------------------------------------------------------//
// Links
//-----------------------------------------------------------------------------------//
a#updraftcentral_addsite_help,
a#updraftcentral_addsite_expertoptions_show {
  @include link();
}

//-----------------------------------------------------------------------------------//
// Dashboard
//-----------------------------------------------------------------------------------//

// Main container
#updraftcentral_dashboard {
  background: $body-bg;
  font-family: $font-family-base;
	font-weight: $font-weight-normal;
	font-size: $font-size-base;
	color: $body-color;

	&.updraft-fullscreen {
	  background: $body-bg;
	}

	a {
		@include link();
		box-shadow: none;
  }

  //-----------------------------------------------------------------------------------//
  // Tables
  //-----------------------------------------------------------------------------------//
  table,
  .table {
    width: 100%;
    text-align: left;
  }

  //-----------------------------------------------------------------------------------//
  // List
  //-----------------------------------------------------------------------------------//
  ul, ol {
    margin: 0 0 1rem 0;
    padding: 0;
  }

  //-----------------------------------------------------------------------------------//
  // Forms
  //-----------------------------------------------------------------------------------//
  label {
    @include label-mixin();
  }

  .label-top {
    @include label-top();
  }

  textarea,
  select,
  input[type=text],
  input[type=password],
  input[type=search],
  input[type=checkbox],
  input[type=radio],
  input[type=number],
  input[type=time]{
    @include field-defaults();
  }

  input[type=number] {
	  width: 60px;
  }

	input[type=text],
	input[type=number],
  input[type=password],
  select {
    @include input-defaults();

	 // Form validation
	 &.field-error {
		 border: 1px solid $brand-primary !important;
	 }
  }

  input[type=checkbox] {
	  @include checkbox-defaults();
  }

  input[type=radio] {
	  @include radio-defaults();
  }

  select {
	  @include select-defaults();
  }

  textarea {
	  @include textarea-defaults();
  }

  // Bootstrap specific
  .form-group {
	  margin-bottom: 0 !important;
  }

  //-----------------------------------------------------------------------------------//
  // Typography
  //-----------------------------------------------------------------------------------//
  h1, .h1,
  h2, .h2,
  h3, .h3,
  h4, .h4,
  h5, .h5,
  h6, .h6 {
    color: $gray-darkest;
    font-family: $font-family-base;
    font-weight: $font-weight-normal;
    margin-bottom: 10px;
  }

  h1, .h1 {
    font-size: $h1-font-size !important;
  }

  h2, .h2 {
    font-size: $h2-font-size !important;
  }

  h3, .h3 {
    font-size: $h3-font-size !important;
  }

  h4, .h4 {
    font-size: $h4-font-size !important;
  }

  h5, .h5 {
    font-size: $h5-font-size !important;
  }

  h6, .h6 {
    font-size: $h6-font-size !important;
  }
  .txt-small {
    @include txt-small();
  }

  .screen-title {
	  font-size: 2.2rem !important;
	  font-weight: 300;
	  float: left;
  }

  .field-error-text {
	  color: $brand-primary !important;
	  font-size: 0.8rem !important;
	  font-weight: $font-weight-normal !important;
	  margin-top: -10px !important;
  }

  //-----------------------------------------------------------------------------------//
  // Buttons
  //-----------------------------------------------------------------------------------//
  .btn {
	  @include btn();

    // Primary button
    &.btn-primary {
      @include btn-primary-mixin();
    }
    // Secondary button
    &.btn-secondary {
      @include btn-secondary-mixin();
    }

    // Primary outline
    &.btn-outline-primary {
      @include btn-outline-primary-mixin();
    }

    // Secondary outline
    &.btn-outline-secondary {
      @include btn-outline-secondary-mixin();
    }

    &.active.focus,
    &.active:focus,
    &.focus,
    &.btn:active.focus,
    &.btn:active:focus,
    &.btn:focus {
      box-shadow: none;
    }

    // Button small
    &.btn-sm  {
	   @include btn-sm-mixin();
    }
  }

  // Various button overrides to make them look like the primary button
  .button-primary,
  #updraftplus-settings-save {
	  @include btn();
	  @include btn-primary-mixin();
  }

  // Add website button
  #updraftcentral_dashboard_newsite {
    &.btn {
      border-radius: 50%;
      width: 55px;
      height: 55px;
      font: 0/0 a;
      padding: 10px;

      .dashicons-plus {
        width: 100%;
        height: 100%;
      }
    }
  }

  // Backup download button
  .updraft_download_button {
    @include btn-outline-secondary-mixin();
    margin-top: 10px;
	 padding: 0.2rem 0.8rem;
	 margin-right: 4px;
  }

  // Backup restore button
  .restore-button {
	  float: none;
	  display: inline-block;

	  @media screen and (min-width: 1200px) {
		  float: left;
		}

	  .button-primary {
		  @include wordpress-button-primary-mixin();
		}
  }

  // Remove button
  .updraftplus-remove {
	  float: none !important;
	  display: inline-block;

	  @media screen and (min-width: 1200px) {
		  float: left;
		}

	  .updraft-delete-link {
		  @include btn-secondary-mixin();
		  height: auto;
		  float: none;

		  @media screen and (min-width: 1200px) {
		  		float: left;
			}
	  }
	}

	.btn {
		// Plugin update into button
		&.update-info {
			background-color: transparent !important;
			color: $gray-dark !important;
			padding: .5rem .5rem;
		}

		// Plugin update queued button
		&.update-failure-messages-text,
		&.update-in-progress {
			display: none;
		}

		// Plugin update
		&.update-in-progress {
			width: 120px !important;
			background-color: transparent;
			border: 1px solid $brand-primary;
			color: $brand-primary !important;
		}
	}

	// Show all site updates
	.updraftcentral_action_show_all_updates {
		.dashicons-cloud {
			&:before {
				content: "";
				width: 20px;
				height: 20px;
				display: inline-block;
				margin: 0;
				vertical-align: top;
				background: url(../images/icons/updates-outline-icon-white.svg)  no-repeat left center;
				background-size: 20px;
			}
		}
	}

  //-----------------------------------------------------------------------------------//
  // Tables
  //-----------------------------------------------------------------------------------//
  table,
  .table {
	  margin-bottom: 15px;

	  @media screen and (min-width: 1200px) {
		  border-bottom: 0 none;
		}

		th,
		td,
		.th,
		.td {
			padding: 12px 0;
			border: 0 none;
			float: none !important;
			position: relative;
		}

		th,
		.th {
			font-size: 0.875rem;
			font-weight: 600;
			text-transform: uppercase;

			@media screen and (min-width: 1200px) {
				border-bottom: 1px solid $gray-light;
			}
		}

		tr,
		.tr {
			border: 0 none !important;
		}

		td,
		.td {
			font-size: 1rem;
			vertical-align: middle;

			&:last-of-type {
				border-bottom: 1px solid $gray-light;
			}

			@media screen and (max-width: 1200px) {
				text-align: left;
			}

			@media screen and (min-width: 1200px) {
				border-bottom: 1px solid $gray-light;
			}

			a {
				margin: 0;
				padding: 0;
			}

			p {
				margin: 0;
				padding: 0;
			}
		}
	}

	//-----------------------------------------------------------------------------------//
	// Header Navigation
	//-----------------------------------------------------------------------------------//
	#updraft-central-navigation {
		background: #fff;
		padding: 10px 2% 10px 18px;
		position: relative;
		z-index: 10;

		.updraftcentral_site_selected {
			display: none;
		}

		.updraft-central-logo {
			width: auto;
			margin: 0;
			padding: 0;

			@media screen and (max-width: 670px) {
				position: absolute;
				left: 50%;
				-webkit-transform: translatex(-50%);
				-moz-transform: translatex(-50%);
				-ms-transform: translatex(-50%);
				-o-transform: translatex(-50%);
				transform: translatex(-50%);
			}

			@media screen and (min-width: 670px) {
				display: inline-block;
				float: left;
				padding-left: 0;
				margin: 0;
			}
		}

		.top_menu_right {
			width: auto;
			float: right;
			white-space: nowrap;
			display: inline-block;
			text-align: right;

			a {
				color: $gray-medium;
				text-decoration: none;

				&:visited {
					color: $gray-medium;
					text-decoration: none;
				}

				&:hover {
					color: $gray-darkest;
					text-decoration: none;
				}
			}

			#updraftcentral_licence_info {
				font-size: 18px;
	      	line-height: 1;
	      	margin-right: 12px;
			}
		}

		.dashicons {
			font-size: 30px;
			color: $gray-medium;
			height: 30px;
			width: 30px;
			cursor: pointer;

			// Help icon
	  		&.updraftcentral-help {
	    		background: url(../images/icons/help-icon.svg) no-repeat center center;
	  		}

	  		// Settings icon
	  		&.updraftcentral-settings {
	    		background: url(../images/icons/settings-icon.svg) no-repeat center center;
	  		}

	  		// Profile icon
	  		&.updraftcentral-profile {
		  		display: none;
	    		background: url(../images/icons/profile-icon.svg) no-repeat center center;
	  		}

	  		// Fullscreen icon
	  		&.updraft-full-screen {
	    		background: url(../images/icons/fullscreen-icon.svg) no-repeat center center;
	  		}

	  		&.dashicons-editor-expand {
		  		&:before {
			  		content: '';
		  		}
		  	}
		}

		.updraft-mobile-menu {
  			color: $gray-dark;
  			float: left;
		}
	}

	//-----------------------------------------------------------------------------------//
	// Content Container
	//-----------------------------------------------------------------------------------//
    &.updraft-fullscreen {
	    #updraft-central-content-container {
	    	height: 100vh;
	    }
    }

	#updraft-central-content-container {
    height: 100vh;


		#updraft-central-content {
			padding: 30px 15px 50px 15px;

			@media screen and (min-width: 670px) {
  		  padding-left: 40px;
        padding-right: 40px;
  		}
		}


    //-----------------------------------------------------------------------------------//
    // Sidebar
    //-----------------------------------------------------------------------------------//

    .updraft-central-sidebar {

		// Sidebar open logo
      .logo {
        display: block;
        display: none;

        img {
          margin-left: auto;
          margin-right: auto;

          &.logo-landscape {
	          display: none;
          }
        }
      }

      // Sidebar folded logo
		&.sidebar-folded {

			.logo {

				img {

					&.logo-portrait {
						display: none;
					}

					&.logo-landscape {
						display: block;
					}
				}
			}
		}

        width: 200px;
        padding-top: 30px;
        float: left;
        overflow-y: auto;
        overflow-x: visible;
        height: 100%;
        background: #fff;
        border: 0 none;
        border-bottom: none;
        display: block;

		  @media screen and (max-width: 670px) {
			  border-right: 1px solid $gray-light;
			}

        // Navigation
        // ======================================================================

        // Hidden Modules container
        .updraft-menu-item-container {
	        &.hidden-modules {
		        border-top: 1px solid $gray-light;

				  .updraft-menu-item {
					  &:hover {
						  border-left: 4px solid #ffffff;
					}
				  }
	        }
        }

        .updraft-menu-item,
        .updraft-menu-item-links,
        .updraft-menu-item-active,
        .updraft-menu-item-links-active {
          background: #fff;
        }

        // Menu label
        .menu-label {
	        margin: 0;
	        padding: 0;
        }

        // Menu items
        .updraft-menu-item {
          height: $nav-link-height;
          font-size: 0.938rem;
          color: $gray-darkest;
          text-align: left;
          line-height: 48px;
          text-transform: uppercase;
          vertical-align: middle;
          padding: 0 0 0 18px;
          border-left: 4px solid #fff;
          cursor: pointer;

          &:hover {
	          background: #fff;
	          color: $gray-darkest;
            border-left: 4px solid $brand-primary;
          }

          .dashicons {
            height: $nav-link-height;
            width: auto;
            margin: 0 6px 0 0;
            color: $gray-darkest;

            &:before {
					content: "";
              height: $nav-link-height;
              width: 20px;
              display: inline-block;
              margin: 0;
              vertical-align: middle;
            }

            &.sites {
              &:before {
                background: url(../images/icons/site-outline-icon.svg)  no-repeat left center;
              }
            }

            &.backups {
              &:before {
                background: url(../images/icons/backup-outline-icon.svg)  no-repeat left center;
              }
            }

            &.updates {
              &:before {
                background: url(../images/icons/update-outline-icon.svg)  no-repeat left center;
              }
            }

            &.updraftvault {
              &:before {
                background: url(../images/icons/vault-outline-icon.svg)  no-repeat left center;
              }
            }

            &.advancedtools {
              &:before {
                background: url(../images/icons/tool-outline-icon.svg)  no-repeat left center;
              }
            }
            &.users {
              &:before {
                background: url(../images/icons/user-outline-icon.svg)  no-repeat left center;
              }
            }
            &.comments {
              &:before {
                background: url(../images/icons/comment-outline-icon.svg)  no-repeat left center;
              }
            }
            &.analytics {
              &:before {
                background: url(../images/icons/analytics-outline-icon.svg)  no-repeat left center;
              }
            }
             &.dashicons-info {
              &:before {
                background: url(../images/icons/upgrade-outline-icon.svg)  no-repeat left center;
              }
            }
            &.wpo {
              &:before {
                background: url(../images/icons/wpo-outline-icon.svg)  no-repeat left center;
              }
            }
            &.modules-open {
              &:before {
                background: url(../images/icons/hidden-modules-icon.svg)  no-repeat left center;
              }
            }
				&.modules-close {
              &:before {
                background: url(../images/icons/hidden-modules-close-icon.svg)  no-repeat left center;
                background-size: 16px;
              }
            }
 				&.dashicons-visibility {
              &:before {
                background: url(../images/icons/view-outline-icon.svg)  no-repeat left center;
                background-size: 19px;
              }
            }

          }
        }

  	    // Active
        .updraft-menu-item-active,
        .updraft-menu-item-links-active {
          border-left: 4px solid $brand-primary;
  	    }

        // Sidebar folded
  	    &.sidebar-folded {
    	    .updraft-menu-item {
            padding: 0 0 0 18px !important;
          }
        }

		  .module-visibility {
				position: absolute;
				cursor: pointer;
				right: 5px;
				top: 5px;
				padding: 7px;
				z-index: 1;
				color: $gray-medium;

				span {

					&:hover {
						color: $gray-medium;
					}
				}
			}

			// Hidden modules menu
			#hidden-modules-menu {

			}
    }
  }

  //-----------------------------------------------------------------------------------//
  // Sidebar collapse
  //-----------------------------------------------------------------------------------//

  @media screen and (min-width: 670px) {
    #updraft-central-sidebar-button {
      display: block;
      position: relative;
      top: 0;
      transform: translatey(50%);
      background-color: #fff;
      width: auto;
      border-radius: 0;
      z-index: 1000;

      .updraft-central-sidebar-button-icon {
        display: inline-block;
        position: relative;
        top: 50%;
        right: 0;
        transform: translateY(0);
        transform: translateX(-50%);
        left: 50%;
      }
    }
  }

  //-----------------------------------------------------------------------------------//
  // Website Pane
  //-----------------------------------------------------------------------------------//
  #updraftcentral_dashboard_existingsites {
    width: 100%;
    display: block;
    margin: 0;
    padding: 0;
    background-color: transparent;
    border-radius: 0;

    .updraftcentral_site_row {
      padding: 12px 12px 15px 12px;
      border: 1px solid $gray-light;
      border-radius: 0;
      text-align: center;
      margin-bottom: 12px;
      position: relative;
      -ms-flex-align: center;
		align-items: center;

      @media screen and (min-width: 1200px) {
        padding: 6px 25px 9px 25px;
        text-align: left;
        justify-content: space-between;
      }

      .updraftcentral_row_container {
        margin-top: 15px;
        margin-right: 0;
        text-align: center;

        @media screen and (min-width: 1200px) {
          margin-top: 4px;
          text-align: right;
        }
      }

      // Drop down row content
      .updraftcentral_row_extracontents {
        width: 100%;
      }

      // Site title
      .updraftcentral_row_sitelabel {
			width: 100% !important;
        padding-left: 0;
        padding-right: 0;

        .updraft_site_title {
          font-size: 1.375rem;
          font-weight: $font-weight-normal;
          display: block;
          max-width: 100% !important;

          @media screen and (min-width: 1200px) {
            min-width: 330px;
            max-width: 500px;
          }
        }

        .updraftcentral_site_url_after_description {
          width: 100%;
          max-width: 100%;
        }
      }

      // Site link
      .updraftcentral_site_url_after_description {
        font-size: 0.875rem;
        padding: 0;
        display: inline-block;
      }

      // Buttons
      .updraftcentral_row_site_buttons {
        padding: 0;
      }

      // Sort Icon
      .updraftcentral_site_sort_icon {
        display: none;
      }
    }

    // Button icons
    .updraftcentral_row_site_buttons {
        width: 100% !important;

      .icon-row-1,
      .icon-row-2 {
        display: inline-block;
      }

      .btn {
        width: 112px;
        padding: 0.25rem 0.5rem;

        @media screen and (min-width: 1200px) {
          width: auto;
        }
      }

      .btn-primary {
        background: transparent;
        color: $gray-darkest !important;
        font-size: 0.813rem !important;

        .dashicons {
          width: 100%;
          height: auto;
          display: block;
          margin-left: auto;
          margin-right: auto;
          margin-bottom: 7px;
        }

        .dashicons {
          &:before {
            content: "";
            height: 20px;
            display: inline-block;
            vertical-align: middle;
          }

        }

        // WP dashboard
        .dashicons-wordpress-alt {
          &:before {
            width: 20px;
            background: url(../images/icons/wordpress-logo-icon.svg) no-repeat center center;
          }
        }

        // More menu
        &.more-option {
			  @media screen and (min-width: 1200px) {
				  height: 57px;
				}

	        .dashicons-menu {
	          @media screen and (max-width: 1200px) {
	            margin-bottom: 0;
	            border-top: 1px solid $gray-light;
	            padding-top: 10px;
	          }

	          &:before {
	            width: 32px;
	            background: url(../images/icons/more-icon.svg) no-repeat center center;
	          }
	        }
			}

        // Existing backup
        .dashicons-existing-backup {
          &:before {
            width: 25px;
            background: url(../images/icons/folder-icon.svg) no-repeat center center;
          }
        }

        // Backup now
        .dashicons-backup-now {
          &:before {
            width: 24px;
            background: url(../images/icons/backup-icon.svg) no-repeat center center;
          }
        }

         // Configure
        .dashicons-configure {
          &:before {
            width: 24px;
            background: url(../images/icons/configure-icon.svg) no-repeat center center;
          }
        }

        // Show updates
        .dashicons-show-updates {
          &:before {
            width: 26px;
            background: url(../images/icons/updates-icon.svg) no-repeat center center;
          }
        }

        // Browse files
        .dashicons-media-document {
          &:before {
            width: 25px;
            background: url(../images/icons/folder-icon.svg) no-repeat center center;
          }
        }

        // Advanced tools
        .dashicons-admin-settings {
          &:before {
            width: 21px;
            background: url(../images/icons/tool-icon.svg) no-repeat center center;
          }
        }

		  // Add user
		  .dashicons-plus {
		    &:before {
		      width: 31px;
		      background: url(../images/icons/add-user-icon.svg) no-repeat center center;
		    }
		  }

		  // Show users
		  .dashicons-admin-users {
		    &:before {
		      width: 20px;
		      background: url(../images/icons/user-icon.svg) no-repeat center center;
		    }
		  }

		  // Show comments
		  .dashicons-admin-comments {
		    &:before {
		      width: 24px;
		      background: url(../images/icons/comments-icon.svg) no-repeat center center;
		    }
		  }

		  // Configure comments
		  .dashicons-admin-tools {
		    &:before {
		      width: 24px;
		      background: url(../images/icons/configure-icon.svg) no-repeat center center;
		    }
