  //-----------------------------------------------------------------------------------//
  // Existing backup screen
  //-----------------------------------------------------------------------------------//

	// Main Container
	.updraft_existing_backups_output {
		margin-top: $screen-content-output-m-t;

		.download-backups {
			padding: 0;
			margin: 0;

		 	// Table container
		 	.updraft_existing_backups {

		 		table, thead, tbody, th, td, tr {
		 			@include responsive-table();
	  			}

	  			tr {
		  			display: none;
	  			}

		 		// Existing backup table
			  .existing-backups-table {

					thead {
					  display: none;
				  }

				  // Main row
				  tr.updraft_existing_backups_row {
					  @media screen and (max-width: 1200px) {
						  display: block;
						}

					  @media screen and (min-width: 1200px) {
						  display: table-row;
						}

					  td {
						  vertical-align: top;
						  text-align: center;

						  @media screen and (min-width: 1200px) {
							  text-align: left;
						  }

						  &.updraft_existingbackup_date {

							  @media screen and (min-width: 1200px) {
								  width: 180px;
								  text-align: left;
							  }
						  }

						  &.before-restore-button {
							  @media screen and (min-width: 1200px) {
								  width: 160px;
							  }
						  }

						  &.updraft_existingbackup_storage {
							  padding-top: 0px;
							  padding-bottom: 0px;

							  @media screen and (min-width: 1200px) {
								  width: 140px;
								  min-width: 140px;
								  padding: 12px 0;
								  padding-right: 20px;
							  }

							  .stored_icon {
								  width: 20px;
								  height: auto;
								  display: inline-block;
								  margin-right: 5px;

								  &:last-of-type {
									  margin-right: 0;
								  }
							  }
						  }

						  &.updraft_existingbackup_data {
							  padding-bottom: 0;

							  @media screen and (min-width: 1200px) {
								  padding: 12px 0;
							  }
						  }

						  // Date checkbox and label
						  .backup_date_label {
							  .delete-backup-container,
							  div {
								  display: inline-block;
								  float: none;

								  @media screen and (min-width: 1200px) {
									  float: left;
								  }
							  }

							  // Backup label
							  .updraft-backup-label {
								  display: none;
							  }
						  }
						}
				  }

				  // View log button
				  .updraft-viewlogdiv {
				  	display: none;
				  }

				  // Backup dropdown
				  a.show-backup-data {

					   > span {
						   width: 17px;
						   height: 17px;
						   font: 0/0 a;
						   margin: 0 5px 0 0;

						   &:before {
							   content: "";
							   width: 100%;
							   height: 100%;
							   display: inline-block;
							   margin-top: 2px;
						   }

						   // Backups closed
						   &.dashicons-arrow-right {
							   &:before {
									background: url(../images/icons/chevron-right-orange.svg) no-repeat center center;
							   }
						   }
						   // Backups open
						   &.dashicons-arrow-down {
							   &:before {
									background: url(../images/icons/chevron-down-orange.svg) no-repeat center center;
							   }
						   }
					   }

						&:hover {
							color: $brand-primary;
						}
				  }

				  // Delete checkbox container
				  .delete-backup-container {
					  margin: 4px 0 0 0;
					  font: 0/0 a;

					  input[type=checkbox] {
						  margin: 0 10px 0 0;
					  }
				  }
				}

				// Bulk delete button
				#btn-backup-bulk-delete {
				  margin-top: 0;
				  margin-bottom: 15px;
				}
			}
		}

		// Rescan button
		.dashicons-image-rotate {
			top: 0;
			left: 0;
		}
	}

	.existing-backups-table thead tr td[colspan="3"],
	.existing-backups-table thead tr td[colspan="4"],
	.existing-backups-table tbody tr td[colspan="4"] {
		display: none;
