		  table, thead, tbody, th, td, tr {
			  @include responsive-table();
		  }

		  @media screen and (max-width: 1200px) {
			  td,
			  .wp-optimize-settings-optimization-checkbox {
				  display: inline-block;
			  }

			  td {
				  width: 80%;
			  }

			  // checkbox td
			  .wp-optimize-settings-optimization-checkbox {
				  width: 25px;
			  }

			  // Optimise description td and run button td
			  .wp-optimize-settings-optimization-info,
			  .wp-optimize-settings-optimization-run {
				  width: 100%;
				  display: block;
			  }

			  // Optimise description td
			  .wp-optimize-settings-optimization-info {
				  padding: 0;
			  }
		  }
		}

		// Table information screen
		#wpoptimize_table_list {

			th,
			td,
			.th,
			.td {
				border-bottom: 1px solid $gray-light;
			}

			// Responsive table
			@media screen and (max-width: 991px) {

				thead {
					border: none;
					clip: rect(0 0 0 0);
					height: 1px;
					margin: -1px;
					overflow: hidden;
					padding: 0;
					position: absolute;
					width: 1px;
				}
			   tr {
					display: block;
					margin-bottom: .625em;
				}
			   td {
					display: block;
					text-align: right;
				}
			   td:before {
					/*
					* aria-label has no advantage, it won't be read inside a table
					content: attr(aria-label);
					*/
					content: attr(data-label);
					float: left;
			  }
			   td:last-child {
					border-bottom: 0;
			  }
			}
