//-----------------------------------------------------------------------------------//
// Modal
//-----------------------------------------------------------------------------------//
#updraftcentral_modal_dialog,
.bootbox.modal {

  a {
    @include link();
  }

  .modal-header,
  .modal-body {

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      font-family: $font-family-base !important;
      font-weight: $font-weight-light !important;
      margin-bottom: 10px;
    }

    p {
	    padding-bottom: 15px;
    }

    .txt-small {
      @include txt-small();
    }
  }

  .modal-header {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      margin-bottom: 0;
    }
  }

  // Modal content
  .modal-content {
    border: 0 none;
    border-radius: 6px;
  }

  .modal-header,
  .modal-body,
  .modal-footer {
    padding: 25px;
  }

  // Modal body
  .modal-body {

    // Form
    label,
    .updraftcentral_addsite_label {
      @include label-mixin();

      &.label-top {
        @include label-top();
      }
    }

	  textarea,
	  select,
	  input[type=text],
	  input[type=password],
	  input[type=checkbox],
	  input[type=radio],
	  input[type=number] {
	    @include field-defaults();
	  }

	  input[type=number] {
		  width: 60px;
	  }

	  input[type=text],
	  input[type=password],
	  select {
	    @include input-defaults();

		 // Form validation
		 &.field-error {
			 border: 1px solid $brand-primary !important;
		 }
	  }

	  input[type=checkbox] {
		  @include checkbox-defaults();
	  }

	  input[type=radio] {
		  @include radio-defaults();
	  }

	  select {
		  @include select-defaults();
	  }

	  textarea {
		  @include textarea-defaults();
	  }

	  // Bootstrap specific
	  .form-group {
		  margin-bottom: 0 !important;
	  }

	  .field-error-text {
	   color: $brand-primary !important;
	   font-size: 0.8rem !important;
	   font-weight: $font-weight-normal !important;
	   margin-top: -10px !important;
		margin-bottom: 10px;
	  }
  }

  // Modal header
  .modal-header {
    background-color: $gray-lightest;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 6px 6px 0 0;

    .modal-title {
      font-size: 1.375rem;
      font-weight: $font-weight-light !important;
      color: $gray-darkest
    }
  }

  // Close button
  .modal-header,
  .modal-body {
	  .close {
		  width: 26px;
		  height: 26px;
		  color: #000;
		  font-size: 1.4rem;
		  font-weight: $font-weight-light;
		  line-height: 0;
		  margin: 0;
		  padding: 0px 0 2px 0;
		  opacity: 0.2;
		  text-shadow: 0px 0px 0px transparent;
		  border: 2px solid #000;
		  border-radius: 50%;

		  &:hover {
        	opacity: 0.2;
        }
    	}
  }

  // Modal footer
  .modal-footer {
    padding-top: 15px;
    border: 0 none;
  }

  // Modal buttons
  .btn-primary,
  .modal-footer .btn-primary {
    @include btn-primary-mixin();
  }
  .btn-secondary,
  .modal-footer .btn-secondary {
    @include btn-secondary-mixin();
  }

  .modal-content	{
	  // Modal with nav
	  .modal-body {

		  // List
		  ul {
			  list-style-type: disc;
			  margin: 0 0 20px 0;
			  padding-left: 20px;
		  }

	    .ui-tabs {
		   width: auto;

	      &.ui-tabs-vertical {

	        // Nav content
	        .ui-tabs-panel {
	          width: 100%;

	          @media screen and (min-width: 576px) {
	            width: 74%;
	          }
	        }

	        // Modal nav
	        .ui-tabs-nav {
	          width: 100%;
	          margin: 0 0 20px 0;
	          padding: 0;
	          border-bottom: 1px solid $gray-lightest;

	          @media screen and (min-width: 576px) {
	            width: 22%;
	            margin: 0 4% 0 0;
	            border: none 0;
	          }

	          li {
	            width: 50%;
	            margin: 0;
	            font-weight: $font-weight-light;
	            float: left;
	            clear: none;
	            text-align: center;

	            @media screen and (min-width: 576px) {
	              width: 100%;
	              display: block;
	              margin-bottom: 1px;
	              clear: left;
	              text-align: left;
	            }

	            &.ui-tabs-active {
	              text-decoration: none;
	              font-weight: $font-weight-light;
	              background-color: $gray-lightest;
	            }

	            a {
	              text-transform: uppercase;
	              text-decoration: none;
	              color: $body-color;
	              padding: 8px 25px;

	              &:hover {
	                text-decoration: none;
	                background-color: $gray-lightest;
	              }
	            }
	          }
	        }
	      }
	    }

	    // Modal with horizontal tabs
	    ul.nav-tabs {
		    @include horizontal-tab-parent();

		    li.nav-item {
			    @include horizontal-wrapper();

			    a {
				    @include horizontal-tab();

					 &:hover {
						 @include horizontal-tab-hover();
					 }
			    }

			    &.selected {
				    a {
					 	@include horizontal-tab-selected();
				    }
			    }

			    // Last tab
				 &:first-child {
					 a {
					 	@include horizontal-tab-first();
					 }
				 }
		    }
		 }
	  }
	}
}

