//-----------------------------------------------------------------------------------//
// Variables
//-----------------------------------------------------------------------------------//

// Colours
$brand-primary:   #DE6726 !default;
$gray-darkest:    #6E7078 !default;
$gray-dark:       #94979F !default;
$gray-medium:     #C6C9D5 !default;
$gray-light:      #EAEDF0 !default;
$gray-lightest:   #F3F5F7 !default;
$white:           #ffffff !default;

// Body
$body-bg:         $gray-lightest !default;
$body-color:      $gray-darkest !default;

// Links
$link-color:            $brand-primary !default;
$link-decoration:       none !default;
$link-hover-color:      darken($link-color, 15%) !default;
$link-hover-decoration: none !default;

// Fonts
$font-family-sans-serif: 'Source Sans Pro', Roboto, "Helvetica Neue", Arial, sans-serif !default;
$font-family-base:       $font-family-sans-serif !default;
$font-size-base: 1rem !default;

$button-font-size:	0.938rem;	

$font-weight-light:       300 !default;
$font-weight-normal:      400 !default;
$font-weight-semibold:    600 !default;
$font-weight-bold:        700 !default;

$h1-font-size:			2.500rem;
$h2-font-size:			1.750rem;
$h3-font-size:			1.375rem;
$h4-font-size:			1.375rem;
$h5-font-size:			1.125rem;
$h6-font-size:			1.000rem;

// Navigation
$nav-link-height:         48px;

// Spacing
$screen-content-output-m-t:	30px;